@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;500;700&family=Yusei+Magic&display=swap);
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

.movies {
  width: 90vw;
  background-color: #f1f2f6;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.movie {
  width: 30vw;
  /* min-width: 500px; */
  max-width: 450px;
  height: 240px;
  margin: 30px 30px;
  background-color: rgb(255, 255, 255);
  display: flex;
  box-shadow: 10px 10px 15px rgb(0, 0, 0, 0.3);
}

.movie img {
  width: 130px;
  height: 200px;
  transform: translateY(-10px);
  margin: 0px 20px;
  box-shadow: 10px 10px 30px rgb(0, 0, 0, 0.3);
}

.movie__data {
  height: 200px;
  margin: 25px 5px;
  margin-right: 30px;
  font-size: 13px;
}
.movie__title,
.movie__date,
.movie__genres,
.movie__summary {
  max-width: 250px;
  text-align: justify;
}

.movie__title {
  font-size: 20px;
  font-weight: 500;
  margin-bottom: 20px;
  color: rgba(0, 0, 0, 0.8);
}
.movie__date,
.movie__genres {
  margin-bottom: 10px;
}

.movie__genres {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
}
.genres__genre {
  margin-right: 5px;
}

.movie__summary {
  line-height: 18px;
}

* {
  box-sizing: border-box;
}

